.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.2);
}
.modal i {
    color: rgb(100,100,100);
    position: fixed;
    top: 10px;
    right: 10px;
}
.modal-content {
    max-width: 450px;
    width: 100%;
    position: fixed;
    top: 50%;
    left:50%;
    background-color: rgb(249,250,251);
    transform: translate(-50%,-50%);
    padding: 1rem;
    border-radius: 15px;
}
.modal-content h1 {
    text-align: center;
}
.btns button {
    width: 100%;
    margin: 0.3rem;
    padding: 10px;
    font-weight: 600;
    border-radius: 6px;
    border: 1px solid;
}
.accept {
    color: #ffff;
    background-color: rgb(42,102,188);
}
.accept:hover{
    background-color: rgb(14,71,149);
}
.reject {
    color: rgb(100,100,100);
}
.reject:hover{
    background-color: rgb(194,192,192);
}